.alert-enter {
    opacity: 0;
    transform: scale(0.9);
}

.alert-enter-active {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 2000ms, transform 2000ms;
}

.alert-exit {
    opacity: 1;
}

.alert-exit-active {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 2000ms, transform 2000ms;
}

.PhoneInputInput:focus{
    outline: none;
}